import Page from 'components/Page';
import SearchInput from 'components/SearchInput';
import React from 'react';
import { Button, Badge, Card, CardBody, CardHeader, Col, Row, Table, Modal,
    ModalBody, ModalFooter, ModalHeader, Input, Label } from 'reactstrap';
import { MdHighlightOff, MdCheckCircle } from 'react-icons/md';

class UnitPage extends React.Component {
    state = {
      modal: false,
      modal_backdrop: false,
      modal_nested_parent: false,
      modal_nested: false,
      backdrop: true,
    };
  
    toggle = modalType => () => {
      if (!modalType) {
        return this.setState({
          modal: !this.state.modal,
        });
      }
  
      this.setState({
        [`modal_${modalType}`]: !this.state[`modal_${modalType}`],
      });
    };
  
    render() {
        return (
            <Page
                title="Unit"
                breadcrumbs={[{ name: 'unit', active: true }]}
                className="UnitPage"
            >
                <Row>
                    <Col>
                    <Card className="mb-3">
                        <CardHeader className="d-flex justify-content-between">
                            <SearchInput />
                            <Button size="sm" onClick={this.toggle('nested_parent')}>Tambah</Button>
                            <Modal
                                isOpen={this.state.modal_nested_parent}
                                toggle={this.toggle('nested_parent')}
                                className={this.props.className}>
                                <ModalHeader toggle={this.toggle('nested_parent')}>
                                    Tambah Unit
                                </ModalHeader>
                                <ModalBody>
                                    <Label>Nama Unit</Label>
                                    <Input type="namaunit" name="namaunit" placeholder="Nama Unit" />
                                </ModalBody>
                                <ModalFooter>
                                    <Button color="primary" onClick={this.toggle('nested')}>
                                    Simpan
                                    </Button>
                                    <Modal
                                    isOpen={this.state.modal_nested}
                                    toggle={this.toggle('nested')}>
                                        <ModalHeader>Konfirmasi Penyimpanan</ModalHeader>
                                        <ModalBody>Apakah Anda yakin ingin menyimpan data ini?</ModalBody>
                                        <ModalFooter>
                                            <Button color="primary" onClick={this.toggle('nested_parent')}>
                                            Ya
                                            </Button>{' '}
                                            <Button
                                            color="secondary"
                                            onClick={this.toggle('nested')}>
                                            Tidak
                                            </Button>
                                        </ModalFooter>
                                    </Modal>
                                    {' '}
                                    <Button color="secondary" onClick={this.toggle('nested_parent')}>
                                    Batal
                                    </Button>
                                </ModalFooter>
                            </Modal>
                        </CardHeader>
                        <CardBody>
                        <Table responsive>
                            <thead>
                            <tr>
                                <th>#</th>
                                <th>Nama Unit</th>
                                <th>Status</th>
                                <th></th>
                            </tr>
                            </thead>
                            <tbody>
                            <tr>
                                <th scope="row">1</th>
                                <td>Unit A</td>
                                <td>
                                    <Badge color="success" className="mr-1">Aktif</Badge>
                                </td>
                                <td>
                                    <Button color="danger" size="sm" onClick={this.toggle('backdrop')}><MdHighlightOff/></Button>
                                    <Modal
                                        isOpen={this.state.modal_backdrop}
                                        toggle={this.toggle('backdrop')}
                                        backdrop={this.state.backdrop}>
                                        <ModalHeader toggle={this.toggle('backdrop')}>Konfirmasi Penonaktifan</ModalHeader>
                                        <ModalBody>
                                            Apakah Anda yakin ingin menonaktifkan data ini?
                                        </ModalBody>
                                        <ModalFooter>
                                            <Button color="primary" onClick={this.toggle('backdrop')}>
                                            Ya
                                            </Button>{' '}
                                            <Button color="secondary" onClick={this.toggle('backdrop')}>
                                            Tidak
                                            </Button>
                                        </ModalFooter>
                                    </Modal>
                                </td>
                            </tr>
                            <tr>
                                <th scope="row">2</th>
                                <td>Unit B</td>
                                <td>
                                    <Badge color="danger" className="mr-1">Tidak Aktif</Badge>
                                </td>
                                <td>
                                    <Button color="success" size="sm" onClick={this.toggle('backdrop')}><MdCheckCircle/></Button>
                                    <Modal
                                        isOpen={this.state.modal_backdrop}
                                        toggle={this.toggle('backdrop')}
                                        backdrop={this.state.backdrop}>
                                        <ModalHeader toggle={this.toggle('backdrop')}>Konfirmasi Pengaktifan</ModalHeader>
                                        <ModalBody>
                                            Apakah Anda yakin ingin mengaktifkan data ini?
                                        </ModalBody>
                                        <ModalFooter>
                                            <Button color="primary" onClick={this.toggle('backdrop')}>
                                            Ya
                                            </Button>{' '}
                                            <Button color="secondary" onClick={this.toggle('backdrop')}>
                                            Tidak
                                            </Button>
                                        </ModalFooter>
                                    </Modal>
                                </td>
                            </tr>
                            <tr>
                                <th scope="row">3</th>
                                <td>Unit C</td>
                                <td>
                                    <Badge color="success" className="mr-1">Aktif</Badge>
                                </td>
                                <td>
                                    <Button color="danger" size="sm" onClick={this.toggle('backdrop')}><MdHighlightOff/></Button>
                                    <Modal
                                        isOpen={this.state.modal_backdrop}
                                        toggle={this.toggle('backdrop')}
                                        backdrop={this.state.backdrop}>
                                        <ModalHeader toggle={this.toggle('backdrop')}>Konfirmasi Penonaktifan</ModalHeader>
                                        <ModalBody>
                                            Apakah Anda yakin ingin menonaktifkan data ini?
                                        </ModalBody>
                                        <ModalFooter>
                                            <Button color="primary" onClick={this.toggle('backdrop')}>
                                            Ya
                                            </Button>{' '}
                                            <Button color="secondary" onClick={this.toggle('backdrop')}>
                                            Tidak
                                            </Button>
                                        </ModalFooter>
                                    </Modal>
                                </td>
                            </tr>
                            </tbody>
                        </Table>
                        </CardBody>
                    </Card>
                    </Col>
                </Row>
            </Page>
        );
    }
}


export default UnitPage;
  